

.bookclassify{
  height: 100vh;
  background-color: #fff;
  display: flex;
  flex-direction: column;
  overflow: hidden;


  .classifylist{
    flex: 1;
    overflow-y: scroll;
    
  }

}

.list{
  width: 95%;
  height: 50px;
  background-color: #ccc;
  border-radius: 10px;
  overflow: hidden;
}


.name{
  width: 75%;
  height: 50px;
  // background-color: green;
  float: left;
  padding-left: 20px;
  line-height: 50px;
  box-sizing: border-box;
}
.option{
  width: 25%;
  height: 50px;
  // background-color: yellowgreen;
  float: left;
  display: flex;
  justify-content: space-around;
  align-items: center;

  .iconfont{
    display: block;
    font-size: 18px;
  }

}


.foot{
  height: 50px;
  // background-color: rgba(0, 0, 0, 0.5);
  position: relative;

  .icon-jia{
    display: block;
    width: 30px;
    height: 30px;
    position: absolute;
    top: 50%;
    margin-top: -15px;
    right: 10px;
    font-size: 30px;
  }
}